<script>
import { ref } from 'vue'
import Board from '@/components/Board'
export default {
  components: { Board },
  setup (props, context) {
    const visibleLeft = ref(false)

    const menu = ref(null)
    const board = ref(null)

    const items = [
      {
        label: 'Options',
        items: [
          {
            label: 'New Backgammon',
            icon: 'pi pi-refresh',
            command: () => {
              board.value.newGame('classic')
            }
          },
          {
            label: 'New Nackgammon',
            icon: 'pi pi-refresh',
            command: () => {
              board.value.newGame('nack')
            }
          },
          {
            label: 'New Hypergammon',
            icon: 'pi pi-refresh',
            command: () => {
              board.value.newGame('hyper')
            }
          }
        ]
      }
    ]

    const toggle = (e) => {
      menu.value.toggle(e)
    }

    return { visibleLeft, toggle, items, menu, board }
  }
}
