<template>

  <div v-if="finish"
       ref="container"
       :class="[
         'finish', row,
         highlight ? 'highlight' : ''
  ]">
    <svg class="rectangle"
         xmlns="http://www.w3.org/2000/svg"
         width='100%'
         height='100%'
         viewBox="0 0 100 100"
         preserveAspectRatio="none"
    >
      <polygon :fill="fill"
               style="paint-order: stroke; stroke: #7e4706; stroke-width: 5px; stroke-linecap: butt; stroke-linejoin: miter;"
               :points="row === 'top' ? '10,0 90,0 90,99 10,99' : '10,1 90,1 90,100 10,100'"
      />
    </svg>
    <template v-for="i in finished" :key="i" style="">
      <Checker :player="player"
               :count="finished"
               :num="i"
               :step-id="id"
               :hint="false"
               :look="'edge'"
               :ref="e => checkers[i] = e"
      />
    </template>
  </div>

  <div v-else-if="bar" :class="['bar', row]" ref="container">
    <svg class="rectangle"
         xmlns="http://www.w3.org/2000/svg"
         width='100%'
         height='100%'
         viewBox="0 0 100 100"
         preserveAspectRatio="none"
    >
      <polygon :fill="fill"
               style="paint-order: stroke; stroke: #7e4706; stroke-width: 5px; stroke-linecap: butt; stroke-linejoin: miter;"
               :points="row === 'top' ? '10,0 90,0 90,99 10,99' : '10,1 90,1 90,100 10,100'"
      />
    </svg>
    <template v-for="i in visible" :key="i">
      <Checker :player="player"
               :count="count"
               :num="i"
               :step-id="id"
               :hint="hint && i === visible"
               :ref="e => checkers[i] = e"
      />
    </template>
  </div>

  <div v-else-if="spacer" :class="['spacer', row]">
    <svg class="rectangle"
         xmlns="http://www.w3.org/2000/svg"
         width='100%'
         height='100%'
         viewBox="0 0 100 100"
         preserveAspectRatio="none"
    >
      <polygon fill="#ffd29f"
               style="paint-order: stroke; stroke: #7e4706; stroke-width: 5px; stroke-linecap: butt; stroke-linejoin: miter;"
               :points="row === 'top' ? '10,0 90,0 90,99 10,99' : '10,1 90,1 90,100 10,100'"
      />
    </svg>
  </div>

  <div v-else
       ref="container"
       :class="[
    'step',
    id % 2 === 0 ? row + ' light light-' + row : row + ' dark dark-' + row,
    highlight ? 'highlight' : ''
  ]">

    <svg :class="'triangle-' + player"
         class="triangle"
         xmlns="http://www.w3.org/2000/svg"
         width='100%'
         height='100%'
         viewBox="0 0 100 100"
         preserveAspectRatio="none"
    >
      <defs>
        <filter :id="'f3' + row + id" x="0" y="0" width="200%" height="200%">
          <feOffset result="offOut" in="SourceAlpha" dx="5" dy="5" />
          <feGaussianBlur result="blurOut" in="offOut" stdDeviation="10" />
          <feBlend in="SourceGraphic" in2="blurOut" mode="normal" />
        </filter>
      </defs>
      <polygon :fill="fill"
               style="paint-order: stroke; stroke: #7e4706; stroke-width: 5px; stroke-linecap: butt; stroke-linejoin: miter;"
               :points="row === 'top' ? points[0] : points[1]"
               :filter="highlight ? 'url(#f3' + row + id + ')' : ''"
      />
    </svg>

    <template v-for="i in visible" :key="i">
      <Checker :player="player"
               :count="count"
               :num="i"
               :step-id="id"
               :hint="hint && i === visible"
               :ref="e => checkers[i] = e"
      />
    </template>
    <div class="step-number">
      <svg style=""
           xmlns="http://www.w3.org/2000/svg"
           width="100%"
           height="20%"
           viewBox="0 0 512 512"
           preserveAspectRatio="xMidYMid meet"
      >
        <text opacity="1"
              style="paint-order: stroke; stroke: #7e4706; stroke-width: 2px; stroke-linecap: butt; stroke-linejoin: miter;"
              x="256"
              :y="row === 'top' ? 136 : 460"
              text-anchor="middle"
              font-family="sans-serif"
              font-weight="bold"
              font-size="120"
              :fill="id % 2 === 0 ? '#7e4706' : '#e8ad69'"
        >
          {{ id }}
        </text>
      </svg>
    </div>
  </div>

</template>

<script>
import { computed, ref, onMounted, onBeforeUpdate } from 'vue'
import Checker from '@/components/Checker'

export default {
  components: { Checker },
  props: {
    id: Number,
    player: String,
    count: Number,
    highlight: Boolean,
    hint: Boolean,
    row: String,
    bar: Boolean,
    spacer: Boolean,
    finish: Boolean,
    finished: Number
  },

  setup (props, context) {
    const points = [
      '100,0 50,100 50,100 0,0',
      '0,100 50,0 100,100 0,100'
    ]

    const visible = computed(() => Math.min(5, props.count))

    const fill = computed(() => {
      if (props.highlight && !props.bar) return '#00ff00'
      if (props.bar || props.finish) return '#ffd29f'

      return props.id % 2 === 0 ? '#e8ad69' : '#7e4706'
    })

    const checkers = ref({})
    const container = ref(null)

    // Make sure to reset the checkers before each update.
    onBeforeUpdate(() => {
      checkers.value = {}
    })

    onMounted(() => {
      // the DOM element will be assigned to the ref after initial render
      // console.log('checkers: ', JSON.parse(JSON.stringify(checkers.value)))
      // if (checkers.value[visible.value] && checkers.value[visible.value].chsvg) {
      //   console.log('REF1: ', checkers.value[visible.value].chsvg, checkers.value[visible.value].chsvg.getBoundingClientRect())
      // }
    })

    return { points, visible, fill, checkers, container }
  }
}

</script>

